.absoluteico {
    position: absolute;
    bottom: 2vw;
    right: 2vw;
    height: 100px;
    width: 100px;
    border-radius: 50px;
    /*background: #b498d4;*/
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.absoluteico--ico{
    width: 80%;
}



